<template>
	<div class="header">
		<img :src="myHeader" alt="">
		{{headerTitle}}
	</div>
</template>

<script>
export default {
	name: 'MHeader',
	data () {
		return {
			myHeader: require('../../assets/images/my-header.jpg'),
			headerTitle: ''
		}
	},
	watch: {
		'$route': {
			handler (newVal, oldVal) {
				this.headerTitle = newVal.meta.title
			},
			immediate: true
		}
	}
}
</script>

